import React from "react";
import { css, styled } from "styled-components";
import Layout from "../../layouts/Layout";
import Button from "../../components/common/button/Button";
import { BUTTON_OPTIONS } from "../../libs/constants/options/options";
import { CONTENT1, CONTENT2, CONTENT3, DiscountOptions, EVENT } from "./subscribeOption";
import { SubscribeInfo } from "./SubscribeInfo";
import promotion from "../../assets/promo.png";
import { DiscountCoupon } from "./DiscountCoupon";

const Subscribe = () => {
  return (
    <Layout>
      <StyledSectionTop>
        <Title>
          Zanit,
          <br />
             
        </Title>

        <figure>
          <img src={promotion} alt=" " />
        </figure>

        <section>
          <p>
               
            <br />
                  .
          </p>

          <p>
               
            <br />
            ()    .
          </p>

          <p>
                 
            <br />
              
            <br />     .
          </p>
        </section>

        <Button {...BUTTON_OPTIONS.SUBSCRIBE}></Button>
      </StyledSectionTop>

      <StyledSectionMiddle>
        <Title>
          Our Service,
          <br />
          Zanit  ?
        </Title>

        <p>
              ,
          <br />
             Bar
          <br />
           1,   4     
        </p>

        <article>
          <DiscountCoupon {...DiscountOptions}></DiscountCoupon>
          <p>
            <strong>
              <time dateTime={EVENT.DATE_TIME}>{EVENT.DATE_OUTPUT_DATE}</time>   
            </strong>
             
            <br />
            25%    !
          </p>
        </article>

        <Button {...BUTTON_OPTIONS.DISCOUNT} />
      </StyledSectionMiddle>

      <StyledSectionBottom>
        <SubscribeInfo {...CONTENT1} />
        <SubscribeInfo {...CONTENT2} />
        <SubscribeInfo {...CONTENT3} />

        <Button {...BUTTON_OPTIONS.SUBSCRIBE_NOW}></Button>
      </StyledSectionBottom>
    </Layout>
  );
};

export default Subscribe;

const Container = css`
  width: 100%;
  padding: 40px 20px;
  box-sizing: border-box;

  & figure {
    margin: 20px 0;
    text-align: center;
  }

  & p {
    margin-bottom: 15px;
    font-family: var(--font--Medium);
    font-size: 14px;
    line-height: 18px;
    white-space: pre-line;
  }

  & button {
    width: 100%;
    margin: 20px auto 30px;
  }
`;

const Title = styled.h2`
  margin-bottom: 20px;
  font-family: var(--font--Bold);
  font-size: 18px;
`;

const StyledSectionTop = styled.section`
  ${Container}
  & figure {
    margin-left: -10px;
  }
`;
const StyledSectionMiddle = styled.section`
  ${Container}
  background-color: #f0f0f0;

  & figure {
    width: 100%;
    & > img {
      width: 100%;
    }

    & > figcaption {
      text-align: left;
    }

    & > figcaption > p {
      display: inline-block;
      padding-left: 5px;
      font-size: 11px;
      color: #404040;
      text-align: left;
      line-height: 13px;
      white-space: pre-line;
    }
  }

  & > article > p {
    margin-bottom: 20px;
    font-family: var(--font--Bold);
    text-align: center;
    white-space: pre-line;

    & strong {
      color: var(--main-color);
    }
  }
`;
const StyledSectionBottom = styled.section`
  ${Container}
`;
